import React, { Component } from "react";
import { Form, FormGroup, Col, Row } from "react-bootstrap";

export default class AntecedentesMedicos extends Component {
  render() {
    return (
      <div>
        <h1>Antecedentes Medicos Generales</h1>
        <Row>
          {this.props.antecedentesMedicosRadioArray.map((line, idx) => {
            return (
              <Col key={idx} xs={6} lg={3}>
                {line.map((radio) => {
                  return (
                    <FormGroup key={radio.label}>
                      <Form.Label>{radio.label}</Form.Label>
                      <br />
                      {radio.checks.map((check) => {
                        return (
                          <Form.Check
                            inline
                            key={check}
                            type="radio"
                            label={check}
                            name={`form${radio.id}Radios`}
                            id={`form${radio.id}Radios${radio.checks.indexOf(
                              check
                            )}`}
                          />
                        );
                      })}
                    </FormGroup>
                  );
                })}
              </Col>
            );
          })}
        </Row>

        <FormGroup>
          <Form.Control
            type="number"
            placeholder="Erupcion Deciduos"
          ></Form.Control>
        </FormGroup>
        <FormGroup>
          <Form.Control type="text" placeholder="Observaciones"></Form.Control>
        </FormGroup>
      </div>
    );
  }
}
