import React, { Component } from "react";
import { Form, FormGroup, Col, Row } from "react-bootstrap";

export default class AnalisisBucal extends Component {
  render() {
    return (
      <div>
        <h1>Analisis Bucal</h1>

        <Row>
          {this.props.analisisBucalRadioArray.map((line, idx) => {
            return (
              <Col key={idx} xs={6} lg={3}>
                {line.map((radio) => {
                  return (
                    <FormGroup key={radio.label} as={Col}>
                      <Form.Label>{radio.label}</Form.Label>
                      {radio.checks.map((check) => {
                        return (
                          <Form.Check
                            key={check}
                            type="radio"
                            label={check}
                            name={`form${radio.id}Radios`}
                            id={`form${radio.id}Radios${radio.checks.indexOf(
                              check
                            )}`}
                          />
                        );
                      })}
                    </FormGroup>
                  );
                })}
              </Col>
            );
          })}
        </Row>

        <Form.Row>
          <FormGroup as={Col} md={6}>
            <Form.Control
              type="text"
              placeholder="Dientes presentes"
            ></Form.Control>
            <Form.Control
              type="text"
              placeholder="Supernumerarios"
            ></Form.Control>
            <Form.Control
              type="text"
              placeholder="Descalcificados"
            ></Form.Control>
            <Form.Control
              type="text"
              placeholder="Oclucion Molar Derecha"
            ></Form.Control>
            <Form.Control
              type="text"
              placeholder="Oclusion Canina Derecha"
            ></Form.Control>
            <Form.Control
              type="text"
              placeholder="Sobremordida Vertical"
            ></Form.Control>
            <Form.Control
              type="text"
              placeholder="Mordida Abierta"
            ></Form.Control>
          </FormGroup>
          <FormGroup as={Col} md={6}>
            <Form.Control
              type="text"
              placeholder="Dientes ausentes"
            ></Form.Control>
            <Form.Control type="text" placeholder="Malformados"></Form.Control>
            <Form.Control type="text" placeholder="Cariados"></Form.Control>
            <Form.Control
              type="text"
              placeholder="Oclucion Molar Izquierda"
            ></Form.Control>
            <Form.Control
              type="text"
              placeholder="Oclusion Canina Izquierda"
            ></Form.Control>
            <Form.Control
              type="text"
              placeholder="Sobremordida Horizontal"
            ></Form.Control>
          </FormGroup>
        </Form.Row>

        <FormGroup>
          <Form.Control type="text" placeholder="Observaciones"></Form.Control>
        </FormGroup>
      </div>
    );
  }
}
