import React, { Component } from "react";
import { Form, FormGroup, Col, Row } from "react-bootstrap";

export default class AnalisisDeModelos extends Component {
  render() {
    return (
      <div>
        <h1>Analisis de Modelos</h1>
        <Form.Row>
          <Col md={6}>
            {this.props.analisisDeModelosDropDownArray
              .slice(0, 6)
              .map((dropdown) => {
                return (
                  <FormGroup key={dropdown.label} as={Row}>
                    <Form.Label column md={5}>
                      {dropdown.label}
                    </Form.Label>
                    <Col md={7}>
                      <Form.Control as="select" custom>
                        {dropdown.fieldArray.map((option) => {
                          return (
                            <option key={option} value={option}>
                              {option}
                            </option>
                          );
                        })}
                      </Form.Control>
                    </Col>
                  </FormGroup>
                );
              })}
          </Col>
          <Col md={6}>
            {this.props.analisisDeModelosDropDownArray
              .slice(5, 12)
              .map((dropdown) => {
                return (
                  <FormGroup key={dropdown.label} as={Row}>
                    <Form.Label column md={5}>
                      {dropdown.label}
                    </Form.Label>
                    <Col md={7}>
                      <Form.Control as="select" custom>
                        {dropdown.fieldArray.map((option) => {
                          return (
                            <option key={option} value={option}>
                              {option}
                            </option>
                          );
                        })}
                      </Form.Control>
                    </Col>
                  </FormGroup>
                );
              })}
          </Col>
        </Form.Row>

        {this.props.analisisDeModelosRadioArray.map((radio) => {
          return (
            <FormGroup key={radio.label}>
              <Form.Label>{radio.label}</Form.Label>
              {radio.checks.map((check) => {
                return (
                  <Form.Check
                    key={check}
                    type="radio"
                    label={check}
                    name={`form${radio.id}Radios`}
                    id={`form${radio.id}Radios${radio.checks.indexOf(check)}`}
                  />
                );
              })}
            </FormGroup>
          );
        })}
      </div>
    );
  }
}
