import React, { Component } from "react";
import { Form, FormGroup, Col } from "react-bootstrap";

export default class ExamenRadiografico extends Component {
  render() {
    return (
      <div>
        <h1>Examen Radiografico</h1>
        <Form.Row>
          <FormGroup as={Col} md={6}>
            <Form.Control
              type="text"
              placeholder="Dientes Presentes"
            ></Form.Control>
            <Form.Control type="text" placeholder="Cariados"></Form.Control>
            <Form.Control type="text" placeholder="Malformados"></Form.Control>
            <Form.Control
              type="text"
              placeholder="Dientes Ausentes"
            ></Form.Control>
            <Form.Control
              type="text"
              placeholder="Supernumerarios"
            ></Form.Control>
            <Form.Control type="text" placeholder="Incluidos"></Form.Control>
          </FormGroup>
          <FormGroup as={Col} md={6}>
            <Form.Control
              type="text"
              placeholder="Resorcion Radicular"
            ></Form.Control>
            <Form.Control type="text" placeholder="Quistes"></Form.Control>
            <Form.Control
              type="text"
              placeholder="Lesiones Periapicales"
            ></Form.Control>
            <Form.Control
              type="text"
              placeholder="Proceso alveolar"
            ></Form.Control>
            <Form.Control
              type="text"
              placeholder="Perdida Cresta Alveolar"
            ></Form.Control>
            <Form.Control
              type="text"
              placeholder="Densidad Osea"
            ></Form.Control>
          </FormGroup>
        </Form.Row>
        <Form.Row>
          {this.props.examenRadiograficoRadioArray.map((radio) => {
            return (
              <FormGroup key={radio.label} as={Col}>
                <Form.Label>{radio.label}</Form.Label>
                {radio.checks.map((check) => {
                  return (
                    <Form.Check
                      key={check}
                      type="radio"
                      label={check}
                      name={`form${radio.id}Radios`}
                      id={`form${radio.id}Radios${radio.checks.indexOf(check)}`}
                    />
                  );
                })}
              </FormGroup>
            );
          })}
        </Form.Row>
        <FormGroup>
          <Form.Control type="text" placeholder="Observaciones"></Form.Control>
        </FormGroup>
      </div>
    );
  }
}
