import React, { Component } from "react";
import { Form, FormGroup, Col } from "react-bootstrap";

export default class PatientData extends Component {
  state = {
    days: [],
    months: [],
    years: [],
  };

  componentDidMount() {
    this.fillDayMonthYearArray();
  }

  fillDayMonthYearArray = () => {
    let days = [];
    let months = [];
    let years = [];
    for (let i = 0; i < 31; i++) {
      days[i] = i + 1;
    }
    for (let i = 0; i < 12; i++) {
      months[i] = i + 1;
    }
    for (let i = 0; i < 102; i++) {
      years[i] = i + 1920;
    }
    this.setState({
      days,
      months,
      years,
    });
  };

  render() {
    return (
      <div>
        <Form.Row>
          <Form.Group as={Col}>
            <Form.Control type="text" placeholder="Nombre Doctor responsable" />
          </Form.Group>

          <Form.Group as={Col}>
            <Form.Control type="text" placeholder="Grupo" />
          </Form.Group>
        </Form.Row>

        <Form.Row>
          <Form.Group as={Col}>
            <Form.Label>Datos de pasiente</Form.Label>
            <Form.Control type="text" placeholder="Nombre" />
            <Form.Control type="text" placeholder="Apellido Paterno" />
            <Form.Control type="text" placeholder="Apellido Materno" />
          </Form.Group>

          <Form.Group as={Col}>
            <Form.Label>Fecha de nacimiento</Form.Label>
            <Form.Control as="select" custom>
              {this.state.days.map((day) => {
                return (
                  <option key={day} value={day}>
                    {day}
                  </option>
                );
              })}
            </Form.Control>
            <Form.Control as="select" custom>
              {this.state.months.map((month) => {
                return (
                  <option key={month} value={month}>
                    {month}
                  </option>
                );
              })}
            </Form.Control>
            <Form.Control as="select" custom>
              {this.state.years.map((year) => {
                return (
                  <option key={year} value={year}>
                    {year}
                  </option>
                );
              })}
            </Form.Control>
          </Form.Group>
          <Form.Group as={Col}>
            <Form.Label>Sexo</Form.Label>
            <Form.Check
              type="radio"
              label="Masulino"
              name="formGenderRadios"
              id="formGenderRadiosMasculino"
            />
            <Form.Check
              type="radio"
              label="Femenino"
              name="formGenderRadios"
              id="formGenderRadiosFemenino"
            />
            <Form.Control type="number" placeholder="Edad" />
          </Form.Group>
        </Form.Row>
        <Form.Group>
          <Form.Control type="text" placeholder="Domicilio" />
        </Form.Group>
        <Form.Row>
          <Form.Group as={Col}>
            <Form.Label>Datos Madre</Form.Label>
            <Form.Control type="text" placeholder="Nombre Madre" />
            <Form.Control type="text" placeholder="Ocupacion" />
            <Form.Control type="text" placeholder="Telefono" />
            <Form.Control type="text" placeholder="Grado Escolar" />
          </Form.Group>
          <Form.Group as={Col}>
            <Form.Label>Datos Padre</Form.Label>
            <Form.Control type="text" placeholder="Nombre Padre" />
            <Form.Control type="text" placeholder="Ocupacion" />
            <Form.Control type="text" placeholder="Telefono" />
            <Form.Control type="text" placeholder="Grado Escolar" />
          </Form.Group>
        </Form.Row>
        <Form.Row>
          <Form.Group as={Col}>
            <Form.Label>Recomendado por</Form.Label>
            <Form.Control type="text" placeholder="Nombre" />
            <Form.Control type="text" placeholder="Apellido Paterno" />
            <Form.Control type="text" placeholder="Apellido Materno" />
          </Form.Group>

          {this.props.datosRadioArray.map((radio) => {
            return (
              <FormGroup key={radio.label} as={Col}>
                <Form.Label>{radio.label}</Form.Label>
                {radio.checks.map((check) => {
                  return (
                    <Form.Check
                      key={check}
                      type="radio"
                      label={check}
                      name={`form${radio.id}Radios`}
                      id={`form${radio.id}Radios${radio.checks.indexOf(check)}`}
                    />
                  );
                })}
              </FormGroup>
            );
          })}
        </Form.Row>
      </div>
    );
  }
}
