import React, { Component } from "react";
import { Form, Button } from "react-bootstrap";

import "../CssComponents/Home.css";

import RadioFields from "../assets/radioFields";

import PatientData from "./PatientData";
import AntecedentesMedicos from "./AntecedentesMedicos";
import AnalisisFacial from "./AnalisisFacial";
import AnalisisBucal from "./AnalisisBucal";
import ExamenRadiografico from "./ExamenRadiografico";
import AnalisisDeModelos from "./AnalisisDeModelos";

export default class Home extends Component {
  state = {
    age: 0,
    datosRadioArray: [],
    antecedentesMedicosRadioArray: [],
    analisisFacialRadioArray: [],
    analisisBucalRadioArray: [],
    examenRadiograficoRadioArray: [],
    analisisDeModelosRadioArray: [],
    analisisDeModelosDropDownArray: [],
  };

  componentDidMount() {
    this.fillRadioArrays();
  }

  fillRadioArrays = () => {
    let datosRadioArray = RadioFields.filter(
      (field) => field.tabla === "datos"
    );
    let antecedentesMedicosRadioArrayEnter = RadioFields.filter(
      (field) => field.tabla === "antecedentesMedicos"
    );

    let chunk = 5;
    let antecedentesMedicosRadioArray = [];
    for (let i = 0; i < antecedentesMedicosRadioArrayEnter.length; i += chunk) {
      antecedentesMedicosRadioArray.push(
        antecedentesMedicosRadioArrayEnter.slice(i, i + chunk)
      );
    }

    chunk = 3;

    let analisisFacialRadioArrayEnter = RadioFields.filter(
      (field) => field.tabla === "analisisFacial"
    );

    let analisisFacialRadioArray = [];
    for (let i = 0; i < analisisFacialRadioArrayEnter.length; i += chunk) {
      analisisFacialRadioArray.push(
        analisisFacialRadioArrayEnter.slice(i, i + chunk)
      );
    }

    chunk = 4;
    let analisisBucalRadioArrayEnter = RadioFields.filter(
      (field) => field.tabla === "analisisBucal"
    );

    let analisisBucalRadioArray = [];
    for (let i = 0; i < analisisBucalRadioArrayEnter.length; i += chunk) {
      analisisBucalRadioArray.push(
        analisisBucalRadioArrayEnter.slice(i, i + chunk)
      );
    }

    let examenRadiograficoRadioArray = RadioFields.filter(
      (field) => field.tabla === "examenRadiografico"
    );
    let analisisDeModelosRadioArray = RadioFields.filter(
      (field) => field.tabla === "analisisDeModelos"
    );
    let analisisDeModelosDropDownArray = [];
    for (let i = 0; i < 11; i++) {
      let maxCantidad = [10, 10, 10, 10, 10, 10, 10, 10, 10, 10, 10, 10];
      let labels = [
        "Sobremordida Vertical",
        "Sobremordida Horizontal",
        "Oclucion Molar",
        "Oclucion Canina",
        "Desviacion Lineal Media",
        "Longitud Arco Basal Maxilar",
        "Longitud Arco Basal Mandibular",
        "Longitud Arco Dental Maxilar",
        "Longitud Arco Dental Mandibular",
        "Discrepancia1",
        "Discrepancia2",
        "Diastemas",
      ];
      let fieldArray = [];
      for (let j = 0; j < maxCantidad[i]; j++) {
        fieldArray.push(j + 1);
      }
      analisisDeModelosDropDownArray.push({ label: labels[i], fieldArray });
    }

    this.setState({
      datosRadioArray,
      antecedentesMedicosRadioArray,
      analisisFacialRadioArray,
      analisisBucalRadioArray,
      examenRadiograficoRadioArray,
      analisisDeModelosRadioArray,
      analisisDeModelosDropDownArray,
    });
  };

  render() {
    return (
      <div id="home-div">
        <h1>Clinica de ortodoncia GNATOS</h1>
        <div id="formContainer-div">
          <Form>
            <PatientData datosRadioArray={this.state.datosRadioArray} />

            <AntecedentesMedicos
              antecedentesMedicosRadioArray={
                this.state.antecedentesMedicosRadioArray
              }
            />

            <AnalisisFacial
              analisisFacialRadioArray={this.state.analisisFacialRadioArray}
            />

            <AnalisisBucal
              analisisBucalRadioArray={this.state.analisisBucalRadioArray}
            />

            <ExamenRadiografico
              examenRadiograficoRadioArray={
                this.state.examenRadiograficoRadioArray
              }
            />

            <AnalisisDeModelos
              analisisDeModelosDropDownArray={
                this.state.analisisDeModelosDropDownArray
              }
              analisisDeModelosRadioArray={
                this.state.analisisDeModelosRadioArray
              }
            />

            <Button variant="primary" type="submit">
              Submit
            </Button>
          </Form>
        </div>
      </div>
    );
  }
}
